* {
    margin:0;
    padding:0;
    box-sizing:border-box;
}

.modal-wrapper{
    background: transparent;
    width:100%;
    max-width: 100vw;
    justify-content: center;
    align-items: center;
    margin:auto;
    position:absolute;
    opacity: 1 !important;
    border-radius: 10px;
}
.modal-header{
    background:green;
    color:#e9e9e9;
    padding:1rem;
    display:flex;
    align-items:center;
    justify-content:space-between;
    border-radius: 0px 10px 10px 0px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    width:95%;
}

.modal-header span {
    font-size:1.5rem;
    cursor:pointer;
}

.modal-content{
    padding: 1.5rem 1rem;
}

.modal-body{
    margin-bottom: 1rem;
}

.modal-body h4{
    font-size:1.8rem;
    color:green;
}


.modal-body p {
    font-size:1rem;
    background: #eee;
    padding: 1rem;
    margin: 1rem 0 2rem 0;
    text-align:left;
    border-radius: 10px;
}

.modal-footer button {
    display:block;
    font-size: 1rem;
    margin-left:auto;
    padding: 0.5rem 1.8rem;
    background:green;
    color: #fff;
    border: none;
    outline:none;
    cursor:pointer;
    border-radius: 10px;
}

#blackSheet{
    margin:0px;
    width:100vw;
    height:100vh;
    background-color:rgba(0, 0, 0, .5);
    text-align: center;
    position: absolute;
    display: absolute;
    justify-content: center;
    align-items: center;
    display:none;
    top:0;
    left:0;
}

.hideModal{
    z-index:-1;
    opacity:0;
    animation: hide .2s;
    transform:scale(0);
}
    @keyframes hide {
        from{
            z-index:10;
            transform:scale(1);
            opacity:1;
        } to {
            z-index:-1;
            transform:scale(0);
            opacity:0;
        }
    }


.showModal{
    opacity:1;
    z-index:10;
    animation: show .2s;
    transform:scale(1);
}
    @keyframes show {
        from{
            z-index:-1;
            transform:scale(0);
            opacity:0;
        } to {
            z-index:10;
            transform:scale(1);
            opacity:1;
        }
    }

div.modal-header img#bldgIcon{
    display:block;
    position:absolute;
    z-index:3;
    width:300px;
    height:180px;
    right:180px;
}
